			</div>
		</div>
		<div class="btn">
			<div class="sum">
				<span>合计：<span class="org">￥{{ (price*buy_num) + (depositprice*buy_num)-yhj_price}}</span></span>
			</div>
			<div class="qr" @click="pay()">
				<span>确认订单</span>
			</div>
		</div>
		<!-- 空DIV 底部 -->
    <div class="hight-l"></div> 
			<!-- 优惠券弹窗 --> 
    <div class="mebs" v-if="shous">
      <div class="boxts">
