					</div>
				</div>
				<ul class="ts">
					<li @click="yhj_Oclicks()" :class="show == 1?'acti':''">
						<span>可用优惠券</span>
					</li>
					<li @click="yhj_vanishs()" :class="show == 0?'acti':''"> 
						<span>不可用优惠券</span>
					</li>
				</ul>
				<!-- 可用优惠券 -->
				<div class="list" v-if="show==1">
					<div v-for="(item , index ) in press" :key="item.id">
						<div class="bar" v-if="zong >item.price">
							<div class="lf">
								<div class="pic" :style ="note">
									<span class="fh">￥{{item.price}}</span>
								</div>
							</div>
							<div class="xx">
								<span class="txt">{{item.title}}</span>
								<span class="time">{{item.createtime*1000 |date}}-{{item.abletime*1000 |date}}</span>
							</div>
							<div class="xz" @click="ChangeMenu(index)" :class="yhj_index == index?'actives':''">
							</div>
						</div>
					</div>
					<!-- 无优惠券状态 -->
					<div class="qs" v-if="keyong.length==0">
						<div class="pic">
							<img src="../../assets/images/commn/qs.png" alt=""> 
						</div>
						<span>暂无可用优惠券~</span>
					</div>
				</div>
				<!-- 不可用优惠券 -->
				<div class="lists" v-if="show==0">
					<div v-for="(item , index ) in press" :key="item.id">
						<div class="bar" v-if="zong <item.price">
							<div class="lf">
								<div class="pic" :style ="notes">
									<span class="fh">￥{{item.price}}</span>
								</div>
							</div>
							<div class="xx">
								<span class="txt">{{item.title}}</span>
								<span class="time">{{item.createtime*1000 |date}}-{{item.abletime*1000 |date}}</span>
							</div>
							<!-- <div class="xz" @click="ChangeMenu(index)" :class="yhj_index == index?'actives':''">
							</div> -->
						</div>
					</div>
					<!-- 无优惠券状态 -->
					<div class="qs" v-if="not.length==0">
						<div class="pic">
							<img src="../../assets/images/commn/qs.png" alt=""> 
						</div>
						<span>暂无可用优惠券~</span>
					</div>
				</div>
				<div class="qd" @click="vanishs(1)">
					<span>确定</span>
				</div>
      </div>
    </div>
