</template>

<script>
import wx from "weixin-js-sdk";
import moment from 'moment';
import 'moment/locale/zh-cn'
moment.locale('zh-cn')
export default {
  name: 'order_form',
  data () {
    return {
			show: 1,
			idx:0,
			keyong:[],//可用优惠券
			not:[],//不可用优惠券
			checkbox1:'',
			sunm:0,
			yhj_index:100,
			shous: false,
			buy_num: 1,//立即购买
			logoimage:"",
			title:"",
			price :"",
			depositprice :"",
			shopname :"",
			name :"",
			address :"",
			shop_phone :"",
			yhj_price:0,//优惠卷 
			yhj_id:'',
			eid:'',
			order_num:'',
			curYUJ:0,
			zong:'',
			site:'',//地址 
			note: {
        backgroundImage: "url(" + require("../../assets/images/homepage/juan.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 1.75rem", 
			},
			notes: {
        backgroundImage: "url(" + require("../../assets/images/homepage/juan_hui.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 1.75rem", 
			},
			press:[
				
			],
    }
	},
	filters:{
		date(val){
			return moment(val).format(("YYYY-M-D H:mm"))
		},
		formatDate(time) {
			return moment(time).format("Do")
		}
	},
  methods: {
		back: function() {
			this.$router.go(-1);
    },
		vanishs: function(flag) {
			this.shous = false;
			if(flag)
			{
				this.curYUJ = this.yhj_price;
			}
		},
		Oclicks: function() {
      this.shous = true;
		},
		ChangeMenu: function(index) {
			// debugger
			let self = this;
			self.yhj_index = index;
			self.yhj_price = self.press[self.yhj_index].price;
			self.yhj_id = self.press[self.yhj_index].id;
			console.log(1111,self.yhj_price ,self.yhj_id)
		},
		//可用  keyong
		yhj_Oclicks: function() {
			this.show = 1;
			for(var i =0; i<this.press.length;i++){
				if(this.zong>this.press[i].price){
					this.keyong.push(this.press[i].price)
				}
			}
			this.not.length=0;
			console.log(9999,this.keyong,this.zong)
		},
		//不可用 not
		yhj_vanishs: function() {
			this.show = 0;
			for(var i =0; i<this.press.length;i++){
				if(this.zong<this.press[i].price){
					this.not.push(this.press[i].price)
				}
			}
			this.keyong.length=0;
			console.log(8888,this.not)
		},
		// 优惠卷
		discounts: function() {
     let self = this;
			let url = "/api/coupon/index?type=" +0; 
			self.httpGet(url,function(res) {
				if (res.code == 1) {
					self.press = res.data;
					self.yhj_Oclicks()
					if(self.press.length>0)
					{
						self.curYUJ = self.press[0].price;
					}
				} else {
					self.$dialog.toast({ 
						mes: res.msg,
						timeout: 1500,
						icon: "error"
					});
				}
			});
		},
		// 地址
		getaddress: function(order_num) {
      let self = this;
      let url = "/api/order/orderconfirm?order_sn=" + this.order_num;
      self.httpGet(url,function(res) {
        if (res.code == 1) {
					self.site = res.data
        } else {
          self.$dialog.toast({
            mes: res.msg,
            timeout: 1500,
            icon: "error"
          });
        }
      });
		},
		// 订单页
		myOrderClick: function() {
			this.$router.push("myOrder"); 
		},
		// 支付
		pay: function(order_num,yhj_id) { 
      let self = this;
      let url = "/api/wechat_pay/wxpayjs?order_sn=" +this.order_num + "&coupon_record_id=" +this.yhj_id;
      self.httpGet(url,function(res) {
        if (res.code == 1) {
					wx.config({
						debug: false,  
						appId: res.data.appId, 
						timestamp: res.data.timestamp,  
						nonceStr: res.data.nonceStr, 
						signature: "0391d095c8903f6202993ceef28e593978684bc5",
						jsApiList: [
							'chooseWXPay'
						] 
					});
					wx.chooseWXPay({
						timestamp: res.data.timeStamp, 
						nonceStr: res.data.nonceStr, 
						package: res.data.package, 
						signType: res.data.signType, 
						paySign: res.data.paySign, 
						success: function (res) {
							self.myOrderClick()
						}
					});
        } else {
          self.$dialog.toast({
            mes: res.msg,
            timeout: 1500,
            icon: "error"
          });
        }
      });
		},
  },
  created() {
		this.discounts();
		
		this.buy_num = this.$route.query.buy_num;//商品数量
		this.logoimage = this.$route.query.logoimage;
		this.title = this.$route.query.title;
		this.price = this.$route.query.price;//商品租赁价格
		this.depositprice = this.$route.query.depositprice;//押金
		this.zong = (this.price*this.buy_num);
		
		this.eid = this.$route.query.eid;
		this.order_num = this.$route.query.order_num;
		this.shopname = localStorage.getItem("shopname");
		this.name = localStorage.getItem("name");
		this.address = localStorage.getItem("address");
		this.shop_phone = localStorage.getItem("shop_phone");
		
		this.getaddress(this.order_num);

		// this.yhj_Oclicks()
	}
}
</script>


